@charset "UTF-8";
/** Media Queries **/
.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  text-align: center;
  justify-content: center;
}
.dashboard__nav {
  display: flex;
  flex-direction: column;
}
.dashboard__nav .enlace {
  all: unset;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  cursor: pointer;
}
.dashboard__nav .enlace:hover {
  color: #007df4;
}
@media (min-width: 768px) {
  .dashboard__nav .enlace {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .dashboard__nav .enlace:last-of-type {
    margin-right: 0;
  }
}
.dashboard__nav .enlace:last-of-type {
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .dashboard__nav {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  cursor: pointer;
}
.dashboard__submit--logout:hover {
  color: #007df4;
}
@media (min-width: 768px) {
  .dashboard__submit--logout {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .dashboard__submit--logout:last-of-type {
    margin-right: 0;
  }
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #40587f;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
  text-align: center;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 1rem 3rem 1rem;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  padding: 1rem 2.5rem;
  text-transform: uppercase;
  font-size: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__contenedor-privilegios, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 2rem;
}
.dashboard__formulario {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.dashboard__grafica {
  width: min(95%, 60rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .dashboard__contenedor-grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.dashboard__contenedor-privilegios {
  width: min(95%, 60rem);
  margin: 0 auto;
  margin-bottom: 5rem;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: center;
}
.table__tr {
  text-align: center;
}
.table__tr:nth-of-type(even) {
  background-color: #eaeef3;
}
.table__td, .table__td--acciones-th, .table__td--acciones-propiedades, .table__td--acciones {
  padding: 1.5rem 1rem;
  font-size: 1.2rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
}
.table__td--acciones-propiedades {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 1rem;
  gap: 2rem 0;
}
.table__td--acciones-th {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.table__td--acciones-th-div {
  display: flex;
  gap: 0 2rem;
}
.table__imagen {
  width: 15rem;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
  display: flex;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion--privilegios {
  color: #323C4B;
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #007df4;
  font-size: 1.6rem;
}
.paginacion__enlace--texto {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #007df4;
}
.paginacion__enlace--texto:hover {
  background-color: #007df4;
  color: #FFFFFF;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #64748B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.bloques {
  width: min(95%, 140rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bloques {
    margin-top: 10rem;
  }
}
.bloques__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .bloques__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.bloque {
  padding: 3rem;
  color: #FFFFFF;
  border-radius: 1rem;
}
.bloque:nth-child(1) {
  background-image: linear-gradient(to left top, #F97316, #fba061);
}
.bloque:nth-child(2) {
  background-image: linear-gradient(to left top, #A855F7, #cd9efa);
}
.bloque:nth-child(3) {
  background-image: linear-gradient(to left top, #59910b, #85d810);
}
.bloque:nth-child(4) {
  background-image: linear-gradient(to left top, #17769E, #23a7df);
}
.bloque__heading {
  margin: 1rem 0 2rem 0;
  font-size: 3.6rem;
}
.bloque__texto {
  margin: 1rem 0;
}
.bloque__texto--cantidad {
  margin-top: 5rem;
  font-weight: 900;
  font-size: 6rem;
  text-align: center;
}

.no-webp .resumen__imagen {
  background-image: url(../img/header.jpg);
}

.webp .resumen__imagen {
  background-image: url(../img/header.webp);
}

.resumen {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .resumen {
    display: grid;
    gap: 5rem;
    grid-template-columns: 2fr 1fr;
  }
}
.resumen__imagen {
  background-position: center center;
  background-size: cover;
  border-radius: 1rem;
  min-height: 40rem;
}
.resumen__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(6, 1fr);
}
@media (min-width: 1024px) {
  .resumen__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resumen__bloque {
  text-align: center;
  padding: 1.5rem;
  border-radius: 1rem;
  background-color: #001F5B;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.resumen__texto, .resumen__texto--numero {
  color: #FFFFFF;
  font-weight: 900;
  line-height: 1;
  margin: 0;
  font-size: 1.2rem;
  text-transform: uppercase;
}
.resumen__texto--numero {
  font-size: 4rem;
}

