html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

main {
  flex-grow: 1; /* Hace que el contenido principal ocupe todo el espacio disponible */
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: #1a1b15;
}

img,
picture {
  width: 100%;
}

.swal2-popup {
  font-size: 2rem !important;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0 2.5rem 0;
  font-weight: 700;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2.2rem;
  text-align: center;
}

h4 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 1.5rem;
  margin: 1rem 0 1rem 0;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

.contenedor-md {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.contenedor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.alinear-centro {
  text-align: center;
}

.w-100 {
  width: 100%;
}

.admin {
  margin-top: 4rem;
  width: 90%;
  border-spacing: 0;
  text-align: center;
}
.admin thead {
  background-color: #001F5B;
}
.admin thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.admin thead tr {
  text-align: center;
}
.admin .imagen-tabla {
  width: 10rem;
}

.bold {
  font-weight: bold;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.5rem;
  width: auto;
  align-self: flex-start;
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.boton {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0.3rem 0;
  border-radius: 0.5rem;
}
.boton:hover {
  background-color: #000e28;
  cursor: pointer;
}

.boton-width {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0.3rem 0;
  border-radius: 0.5rem;
  width: 100%;
}
.boton-width:hover {
  background-color: #000e28;
  cursor: pointer;
}

.boton-eliminar {
  background-color: rgb(169, 0, 0);
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0.3rem 0;
  border-radius: 0.5rem;
}
.boton-eliminar:hover {
  background-color: #760000;
  cursor: pointer;
}

.boton-sesion {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 3rem 0;
  border-radius: 0.5rem;
}
.boton-sesion:hover {
  background-color: #000e28;
  cursor: pointer;
}

.boton-inline-block {
  background-color: #001F5B;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1.5rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
